@import "../../common/sass";

.container{
  width: 80%;
  border: 1px solid var(--secondColor);
  height: 30%;
  border-radius: 10px;
  padding: 5px 10px;
  overflow: auto;
  overflow-x: hidden;
  @include scrollbar();
  @media screen and (max-width: $small) {
    height: 40%;
  }

  legend{
    color: var(--secondColor);
    font-size: 10px;
    text-align: center;
    position: relative;
    padding-bottom: 10px;

  }
  .usersPanel{
    width:100%;
    display: flex;
    flex-direction: column;
    gap: 5px;
    justify-content: flex-start;

    .userName{
      border: none;
      border-radius: 20px;
      width: 90%;
      word-wrap: break-word;
      padding: 0 10px;
      background-color: var(--secondColor);
      color: var(--textMainColor);
      span{
        display: none;
        @media screen and (max-width: $small) {
          display: flex;
        }
      }
      @media screen and (max-width: $small) {
        font-size: 24px;
        display: flex;
        justify-content: space-between;
      }
    }
  }
}
